import React, { Component } from 'react';
import { Modal } from 'antd-mobile';
import Game from './Game'

class GamePage extends Component {
    constructor(props) {
        super(props);
        this.levelSuccess = this.levelSuccess.bind(this);
        this.gameOver = this.gameOver.bind(this);
    }

    initCanvas() {
        this.canvas = document.getElementById("time_graph_canvas");
        if(!this.game) {
            this.game = new Game(this.canvas, false);
            this.game.levelSuccessHandle = this.levelSuccess;
            this.game.gameOverHandle = this.gameOver;
        }
        this.game.init();
        this.game.gameStart();
    }

    levelSuccess() {
        if(this.game.level < 4) {
            this.game.gameContinue(true);
        } else {
            Modal.alert('Congratulations', '恭喜你成功通关', [
                { text: '再玩一次', onPress: () => {
                    this.game.init();
                    this.game.gameStart();
                } },
            ]);
        }
    }

    gameOver() {
        Modal.alert('Game Over', '游戏结束', [
            { text: '重新开始', onPress: () => {
                this.game.init();
                this.game.gameStart();
            } },
        ]);
    }

    componentDidMount() {
        this.initCanvas()
    }

    componentDidUpdate() {
    }

    render() {
        return(
            <div>
                <canvas id="time_graph_canvas" />
            </div>
        );
    }
}

export default GamePage